/*
==============================
Default CSS
==============================
*/
:root {
  scroll-behavior: initial;
}

body {
  padding: 0;
  margin: 0;
  font-family: "Poppins", sans-serif;
  background-color: white;
}

.h1, .h2, .h3, .h4, .h5, .h6, h1, h2, h3, h4, h5, h6 {
  margin-bottom: 0;
  font-weight: 400;
  line-height: 1;
}

ul {
  padding-left: 0;
  margin-bottom: 0;
}

ul li {
  list-style: none;
}

:focus {
  outline: 0 !important;
}

p {
  margin-bottom: 0;
  font-weight: 300;
  line-height: 1.8;
  color: #666666;
}

a {
  color: #222222;
  text-decoration: none;
  transition: .4s;
}

a:hover, a:focus {
  color: #f1822f;
  text-decoration: none;
}

img {
  max-width: 100%;
}

.ptb-80 {
  padding-bottom: 80px;
  padding-top: 80px;
}

.d-table {
  width: 100%;
  height: 100%;
}

.d-table-cell {
  vertical-align: middle;
}

.bg-f6f6f6 {
  background: #f6f6f6;
}

.form-group {
  margin-bottom: 20px;
}

.form-group label {
  margin-bottom: 9px;
}

.relative {
  position: relative;
}

/*Btn Btn-Primary*/
.btn {
  padding: 9px 25px;
  font-size: 16px;
  border-radius: 30px;
  transition: .4s;
}

.btn-primary {
  color: #fff;
  background-color: #f1822f;
  border-color: #f1822f;
}

.btn-primary:hover, .btn-primary:focus, .btn-primary.disabled:hover, .btn-primary.disabled:focus {
  color: #f1822f;
  background-color: #fff;
  border-color: #f1822f;
}

.btn-primary:not(:disabled):not(.disabled).active, .btn-primary:not(:disabled):not(.disabled):active {
  color: #fff;
  background-color: #f1822f;
  border-color: #f1822f;
}

.btn-primary:not(:disabled):not(.disabled).active:focus, .btn-primary:not(:disabled):not(.disabled):active:focus {
  box-shadow: none;
}

.btn-primary.focus, .btn-primary:focus {
  box-shadow: none;
}

.btn-primary.disabled, .btn-primary:disabled {
  color: #fff;
  background-color: #f1822f;
  border-color: #f1822f;
  opacity: 1;
}

/* Section Title */
.section-title {
  margin-bottom: 60px;
  padding-left: 30px;
  position: relative;
}

.section-title::before, .section-title::after {
  content: '';
  position: absolute;
  width: 3px;
  height: 100%;
  left: 10px;
  top: 0;
  background: #f1822f;
  transition: .4s;
}

.section-title::after {
  left: 0;
  height: 50%;
}

.section-title:hover::before {
  height: 50%;
}

.section-title:hover::after {
  height: 100%;
}

.section-title h2 {
  font-size: 35px;
  font-weight: 200;
  margin-bottom: 20px;
}

.section-title h2 span {
  color: #f1822f;
  font-weight: 600;
}

.section-title p {
  margin-top: -7px;
}

.section-title h4 {
  font-size: 18px;
  font-weight: 300;
  color: #f1822f;
  margin-bottom: 17px;
  text-transform: capitalize;
}

/* Form Control */
.form-control {
  height: 45px;
  padding: 10px 20px;
  font-size: 15px;
  background-color: #ffffff;
  border: 1px solid #eeeeee;
  border-radius: 0;
}

.form-control:focus {
  border-color: #dddddd;
  outline: 0;
  box-shadow: none;
}

textarea.form-control {
  min-height: auto;
  height: auto !important;
}

/* Owl Carousel Button */
.owl-theme .owl-nav {
  margin-top: 0;
  border-radius: 50%;
}

.owl-theme .owl-dots .owl-dot span {
  width: 15px !important;
  height: 15px !important;
  margin: 0 4px !important;
  background: transparent !important;
  border-radius: 50% !important;
  border: 2px solid #dddddd !important;
  transition: .4s !important;
}

.owl-theme .owl-dots .owl-dot.active span {
  background: #869791 !important;
  border-radius: 5px !important;
  width: 35px !important;
  height: 10px !important;
  border-color: #f1822f !important;
  background: #f1822f !important;
}

.owl-theme .owl-dots .owl-dot:hover span {
  background: #f1822f !important;
  border-color: #f1822f !important;
}

.owl-theme .owl-nav [class*="owl-"] {
  position: absolute;
  left: 40px;
  top: 50%;
  margin-top: -20px;
  background: rgba(255, 255, 255, 0.33) !important;
  width: 60px;
  text-align: center;
  height: 60px;
  line-height: 54px;
  border-radius: 50% !important;
  font-size: 30px;
  color: #ffffff !important;
  transition: .4s;
}

.owl-theme .owl-nav [class*="owl-"]:hover {
  background: #f1822f !important;
  color: #ffffff !important;
}

.owl-theme .owl-nav .owl-next {
  left: auto;
  right: 40px;
}

/*
==============================
Preloader CSS
==============================
*/
.preloader {
  position: fixed;
  width: 100%;
  height: 100%;
  background: #ffffff;
  z-index: 99999;
}

.preloader .folding-cube {
  left: 0;
  width: 60px;
  height: 60px;
  position: absolute;
  -webkit-transform: rotateZ(45deg);
          transform: rotateZ(45deg);
  top: 50%;
  right: 0;
  margin: -60px auto 0;
}

.preloader .folding-cube .cube {
  float: left;
  width: 50%;
  height: 50%;
  position: relative;
  -webkit-transform: scale(1.1);
          transform: scale(1.1);
}

