.example-demo-modal {
  position: fixed;
  overflow: hidden;
  right: 0;
  top: 0;
  z-index: 99999;
  transition: .9s;
  opacity: 0;
  visibility: hidden;
  border-left: 1px solid #eeeeee;
  right: -100%;
}

.example-demo-modal .inner {
  width: 450px;
  height: 100vh;
  overflow-y: scroll;
  background-color: #fff;
  box-shadow: 0 0 30px 0 rgba(67, 67, 67, 0.15);
  padding: 90px 30px 30px 30px;
}

.example-demo-modal .inner ul {
  display: flex;
  flex-wrap: wrap;
  padding-left: 0;
  margin-bottom: 0;
  list-style-type: none;
  margin-left: -10px;
  margin-right: -10px;
  margin-bottom: -20px;
}

.example-demo-modal .inner ul li {
  flex: 0 0 50%;
  max-width: 50%;
  padding-left: 10px;
  padding-right: 10px;
  padding-bottom: 20px;
}

.example-demo-modal .inner ul li .single-demo {
  position: relative;
  text-align: center;
  border-radius: 5px;
}

.example-demo-modal .inner ul li .single-demo img {
  border-radius: 5px;
  box-shadow: 0 0.5rem 1rem rgba(0, 0, 0, 0.15);
  border: 5px solid rgb(255, 255, 255)255, 255, 255);
}

.example-demo-modal .inner ul li .single-demo::before {
  content: '';
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  background-color: #ff792c;
  transition: .5s;
  opacity: 0;
  visibility: hidden;
  border-radius: 5px;
}

.example-demo-modal .inner ul li .single-demo span {
  position: absolute;
  left: 0;
  right: 0;
  top: 50%;
  -webkit-transform: translateY(-50%);
          transform: translateY(-50%);
  z-index: 1;
  color: #fff;
  text-transform: capitalize;
  transition: .5s;
  opacity: 0;
  visibility: hidden;
  margin-top: 15px;
  font-size: 16px;
  font-weight: 600;
}

.example-demo-modal .inner ul li .single-demo .link-btn {
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  display: block;
  z-index: 3;
}

.example-demo-modal .inner ul li .single-demo:hover::before {
  opacity: 1;
  visibility: visible;
}

.example-demo-modal .inner ul li .single-demo:hover span {
  opacity: 1;
  visibility: visible;
  margin-top: 0;
}

.example-demo-modal .header-title {
  position: absolute;
  background-color: #fff;
  top: -1px;
  left: 0;
  right: 0;
  z-index: 5;
  border-bottom: 1px solid #eeeeee;
  padding-top: 18px;
  padding-bottom: 15px;
  padding-left: 30px;
  padding-right: 30px;
}

.example-demo-modal .header-title .example-demo-modal-control {
  position: absolute;
  right: 25px;
  top: 15px;
  font-size: 20px;
  color: #000;
  transition: .5s;
  background-color: transparent;
  border : none;
  padding: 0;
  display: inline-block;
}

.example-demo-modal .header-title .example-demo-modal-control:hover {
  color: #f1822f;
}

.example-demo-modal .header-title .title {
  color: #000;
  margin-bottom: 0;
  font-size: 18px;
  font-weight: 600;
}

.example-demo-modal.active {
  right: 0;
  opacity: 1;
  visibility: visible;
}
